<template>
<div id = 'iframe' class="ifram-container" >
    <!-- <span @click="$router.push('/map')" class="back">返回</span>  -->
    <iframe :src="url" frameborder="0" width="100%" height="100%">
    </iframe>
</div>
</template>
<script>
export default {
  created () {
    this.url = window.urlsPath.statisticsUrl
  },
  data () {
    return {
      url: ''
    }
  }
}
</script>
<style scoped>
.back{
    position: absolute;
    color: white;
    top: 10px;
    left: 10px;
}
.ifram-container {
    height: 100vh;
    width: 100%;
}
</style>


